.depcode {
  position: relative;
  font-weight: 600;
}
.depname {
  position: relative;
  font-size: var(--font-size-xs);
  color: var(--color-lightslategray);
}
.departure {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-11xs);
}
.flightIcon {
  position: relative;
  width: 48.83px;
  height: 11.96px;
}
.arrname {
  position: relative;
  font-size: var(--font-size-xs);
  color: var(--color-lightslategray);
  text-align: right;
}
.arrival,
.locations {
  display: flex;
  justify-content: flex-start;
}
.arrival {
  flex: 1;
  flex-direction: column;
  align-items: flex-end;
  gap: var(--gap-11xs);
}
.locations {
  align-self: stretch;
  border-radius: var(--br-9xs);
  background-color: var(--color-whitesmoke-100);
  flex-direction: row;
  padding: var(--padding-5xs);
  align-items: center;
  gap: var(--gap-6xs);
  text-align: left;
  font-size: var(--font-size-base);
  color: var(--color-cornflowerblue-100);
  font-family: var(--font-inter);
}
